import { transformResponse } from ".";
import {
  IServerResponse,
  Overwrite,
  EmailNotification,
  IGetAllBackupJobsResponseSuccess,
  ICreateBackupJobArgs,
  IBackupJobResponseSuccess
} from "@/shared";
import { IJob } from "../modules/JobsModule";
import { axiosInstance } from "./axios";

let serverJobsList: IJob[] = [
  {
    name: "My daily job",
    id: 20,
    repoId: 3,
    emailNotification: EmailNotification.never,
    maxBackups: 100,
    backupLocations: ["C:/2/0", "C:/2/2/0"],
    repeatPattern: "0 0 10 * * *"
  },
  {
    name: "Weekly backup job",
    id: 21,
    repoId: 3,
    emailNotification: EmailNotification.onerror,
    maxBackups: 100,
    backupLocations: ["D:/", "C:/0", "C:/4"],
    repeatPattern: "0 0 0 * * 1"
  },
  {
    name: "Important files",
    id: 22,
    repoId: 5,
    emailNotification: EmailNotification.always,
    maxBackups: 110,
    backupLocations: [
      "D:/test2",
      "C:/bootmgr",
      "C:/hiberfil.sys",
      "C:/asdfbackupNew Folder/data"
    ],
    repeatPattern: "0 0 */10 * * *"
  }
];

export const getJobsTemp = async (
  userId: number
): Promise<IServerResponse<IGetAllBackupJobsResponseSuccess>> => {
  const response = await axiosInstance.get<
    IServerResponse<IGetAllBackupJobsResponseSuccess>
  >(`/user/${userId}/backupJob`);
  return transformResponse(response);
};

export const createJobTemp = async (
  userId: number,
  repo: ICreateBackupJobArgs
): Promise<IServerResponse<IBackupJobResponseSuccess>> => {
  const response = await axiosInstance.post<
    IServerResponse<IBackupJobResponseSuccess>
  >(`/user/${userId}/repository`, repo);
  return transformResponse(response);
};

export const getJobs = async (): Promise<IServerResponse<{ jobs: IJob[] }>> => {
  await new Promise(resolve => setTimeout(resolve, 200));

  return {
    messages: [],
    payload: {
      jobs: serverJobsList
    }
  };
};

export const createJob = async (
  repo: Overwrite<IJob, { id?: number }>
): Promise<IServerResponse<IJob>> => {
  await new Promise(resolve => setTimeout(resolve, 200));
  const newJob = { ...repo, id: Math.random() };
  serverJobsList = [...serverJobsList, newJob];
  return { messages: [], payload: newJob };
};

export const deleteJob = async (id: number): Promise<IServerResponse> => {
  await new Promise(resolve => setTimeout(resolve, 200));
  serverJobsList = serverJobsList.filter(r => r.id !== id);
  return { messages: [] };
};

export const updateJob = async (job: IJob): Promise<IServerResponse<IJob>> => {
  await new Promise(resolve => setTimeout(resolve, 200));
  serverJobsList = serverJobsList.map(j => (j.id === job.id ? job : j));
  return { messages: [], payload: job };
};
